/* EventList.css */

.button {
  width: calc(100% - 20px); /* Account for margin on both sides */
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
  margin-right: 10px;
  margin-left: 10px;
}

.event-list {
  list-style-type: none;
  padding: 0;
  margin: 0;
}

.event-name,
.event-details,
.closing-info {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.event-name {
  font-weight: bold;
}










